import config from '../../utils/config.js'
export default{
	data(){
		return{
			time:0,
			isShow:true,
			phone:'',
			code:'',
			agree:false,
			validateAll:false,
			lili:false
		}
	},
	computed:{
		isRightPhone () {
		        return /^1\d{10}$/.test(this.phone)
		},
		isRightCode() {
		        return /\d{5}$/.test(this.code)
		}
		
	},
	methods:{
		isAgree(){
			this.agree = !this.agree
		},
		empty(){
			this.phone = ''
		},
		otherLogin(){
			wx.navigateTo({
				url:'../Login/login'
			})
		},
		passwordLogin(){
			wx.navigateTo({
				url:'../LoginPassword/loginPassword'
			})
		},
		async sendcode(){
			const that = this
			if(this.isRightPhone){
				this.lili = false
				this.isShow=false
				this.time = 5
				await wx.request({
					url:`${config.host+'/sendcode'}`,
					data:{'phone':that.phone},
					method:"GET",
					success:function(res){
						console.log('请求成功')
					},
					fail:function(error){
						console.log(error.errMsg)
					}
				})
				const setinterId = setInterval(()=>{
					this.time= this.time-1
					if(this.time<=0){
						clearInterval(setinterId)
						this.isShow=!this.isShow
					}
				},1000)
			}else{
				this.lili = true
			}
		},
		async login(){
			this.validateAll = true
			const that = this
			if(this.isRightCode&&this.isRightPhone&&this.agree){
				wx.login({
					success:function(res){
						if(res.code){
							wx.request({
									url:`${config.host+'/login_sms'}`,
									data:{phone:that.phone,code:res.code},
									method:"POST",
									dataType: "json",
									header: {
									    'content-type': 'application/json' // 默认值
									},
									success:function(res){
										console.log(res.data)
										wx.navigateTo({
											url:'../Ucenter/ucenter'
										})
										wx.setStorage({
											'key':'isLogin',
											'data':true
										})
									},
									fail:function(error){
										console.log('请求失败')
										wx.showToast({
											title: '服务器崩溃啦！',
											icon: 'none',
											duration: 2000
										})
									}
								})
							
						}
					}
				})
								}
		},
		
		
	},
	watch:{
		
	}
}
